import React from 'react'
import { graphql, Link } from 'gatsby'
import get from 'lodash/get'

import Meta from 'components/Meta'
import Layout from 'components/Layout'

import { Container, Section } from '@style/global'

const Landingpage = ({ data, location }) => {
  return (
    <Layout location={location}>
      <Meta site={get(data, 'site.meta')} />
      <Container paddingL>
        <Section>
          <h1>Hello world!</h1>
          <p>Please check out the <Link to="/readme">Readme Page</Link> to learn more about the design system.</p>
          <p><small>This starter is designed and developed by Ansin Lau.</small></p>
        </Section>
      </Container>
    </Layout>
  )
}

export default Landingpage

export const pageQuery = graphql`
  query {
    site {
      meta: siteMetadata {
        title
        description
        url: siteUrl
        author
        twitter
        adsense
      }
    }
  }
`
